import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Work1 from './views/Work1.vue'
import Work2 from './views/Work2.vue'
import Work3 from './views/Work3.vue'

Vue.use(Router)

export default new Router({
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/work1',
      name: 'work1',
      component: Work1
    },
    {
      path: '/work2',
      name: 'work2',
      component: Work2
    },
    {
      path: '/work3',
      name: 'work3',
      component: Work3
    }
  ],
  mode: 'history'
})
